:root {
	--accent: #8BF2FB;
}

.todo {
	height: calc(100vh - 40px);
	width: 800px;
	margin: 20px auto;
	padding: 35px 35px 50px;
	font-family: Arial;
	color: #444;
	-webkit-box-shadow: 0 1px 5px rgba(0,0,0,.3);
	        box-shadow: 0 1px 5px rgba(0,0,0,.3);
	overflow-x: hidden;
}

.todo *::-moz-selection {
	background-color: var(--accent);
	color: #fff;
}

.todo *::selection {
	background-color: var(--accent);
	color: #fff;
}

.todo__title {
	margin-bottom: 40px;
	text-align: center;
	text-transform: uppercase;
	color: #333;
}

.todo-btn {
	padding: 6px 7px;
	background-color: transparent;
	border: 1px solid #555;
	font-size: 11px;
	color: #555;
	text-transform: uppercase;
	outline: none;
	cursor: pointer;
	-webkit-transition: background-color .15s;
	-o-transition: background-color .15s;
	transition: background-color .15s;
}
.todo-btn:not(:first-child) {
	border-left-width: 0px;
}
.todo-btn:hover {
	background-color: var(--accent);
}

.todo__sort {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.todo__sort-btns {
	margin-left: 10px;
}

.todo__sort-btn_active {
	background-color: var(--accent);
}

.todo__menu {
	margin: 30px 0;
}

.todo__task {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding: 8px 0;
	cursor: -webkit-grab;
	cursor: grab;
}
.todo__task:not(:last-child) {
	border-bottom: 1px solid #aaa;
}

.todo__task-index {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	width: 26px;
	height: 26px;
	border: 1.5px solid #444;
	font-weight: 600;
}
.todo__task-index_completed {
	background-color: var(--accent);
}

.todo__task-content {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	padding: 4px 8px;
}

.todo_task-title_completed {
	text-decoration: line-through;
}

.todo__task-date {
	font-style: italic;
}
.todo__task-date_overdue {
	color: #FA3950;
}

.todo__task-controls {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.todo__task-complete-btn_completed {
	background-color: var(--accent);
}

.todo__no-tasks {
	padding: 9.8px 0;
	font-size: 20px;
	font-style: italic;
}


